/* 2025 Futuristic Blockchain Verifier Styles */
.blockchain-verifier-container {
  padding: 2rem;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  min-height: calc(100vh - 160px);
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  overflow: hidden;
}

.blockchain-verifier-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 30% 70%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 70% 30%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(120, 219, 226, 0.3) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

.blockchain-verifier-header {
  text-align: center;
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
}

.blockchain-verifier-header h2 {
  margin: 0;
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 300;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
  background: linear-gradient(45deg, #ffffff, #b794f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.page-navigation {
  margin-bottom: 1.5rem;
  text-align: center;
}

.page-navigation a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
}

.page-navigation a:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.verification-section {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.verification-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.02) 50%, transparent 70%);
  pointer-events: none;
}

.verification-section:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.2);
}

.verification-section h3 {
  margin: 0 0 1.5rem 0;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 300;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

.form-group {
  margin-bottom: 1.5rem;
}

.json-input {
  width: 100%;
  min-height: 200px;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: #ffffff;
  font-size: 1rem;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  resize: vertical;
  line-height: 1.4;
}

.json-input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

.json-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.input-label {
  display: block;
  margin-bottom: 0.5rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 0.9rem;
}

.verify-button {
  background: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  border: none;
  padding: 1rem 2rem;
  color: white;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 500;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  width: 100%;
  max-width: 200px;
}

.verify-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.6);
  background: linear-gradient(45deg, #764ba2 0%, #667eea 100%);
}

.verify-button:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.loading-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: #4ade80;
  font-weight: 500;
  padding: 2rem;
  margin: 1rem 0;
}

.spinner {
  width: 24px;
  height: 24px;
  border: 3px solid rgba(74, 222, 128, 0.3);
  border-radius: 50%;
  border-top-color: #4ade80;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.result-section {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 1.5rem;
  position: relative;
  overflow: hidden;
}

.result-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(34, 197, 94, 0.05) 50%, transparent 70%);
  pointer-events: none;
}

.result-title {
  margin: 0 0 1rem 0;
  color: #4ade80;
  font-size: 1.2rem;
  font-weight: 500;
  text-shadow: 0 0 10px rgba(74, 222, 128, 0.3);
}

.result-content {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(74, 222, 128, 0.2);
  border-radius: 8px;
  padding: 1rem;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 0.9rem;
  color: #e0e0e0;
  white-space: pre-wrap;
  word-break: break-all;
  max-height: 400px;
  overflow-y: auto;
  line-height: 1.4;
}

.result-content::-webkit-scrollbar {
  width: 6px;
}

.result-content::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.result-content::-webkit-scrollbar-thumb {
  background: rgba(74, 222, 128, 0.3);
  border-radius: 3px;
}

.result-content::-webkit-scrollbar-thumb:hover {
  background: rgba(74, 222, 128, 0.5);
}

.error-message {
  background: rgba(255, 107, 107, 0.1);
  border: 1px solid rgba(255, 107, 107, 0.3);
  border-radius: 12px;
  padding: 1rem;
  margin-top: 1rem;
  color: #ff6b6b;
  backdrop-filter: blur(10px);
  font-weight: 500;
}

.hint-text {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 0.5rem;
  font-style: italic;
  text-align: center;
}

.sample-payload {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 1rem;
  margin-top: 1rem;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
  white-space: pre-wrap;
  line-height: 1.3;
}

.footer {
  text-align: center;
  margin-top: 3rem;
  padding: 2rem 1rem;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* Responsive design */
@media (max-width: 768px) {
  .blockchain-verifier-container {
    padding: 1rem;
  }

  .blockchain-verifier-header h2 {
    font-size: 2rem;
  }

  .verification-section {
    padding: 1.5rem;
  }

  .json-input {
    min-height: 150px;
    font-size: 0.9rem;
  }

  .verify-button {
    width: 100%;
    max-width: none;
  }

  .result-content {
    font-size: 0.8rem;
    max-height: 300px;
  }
}