/* 2025 Futuristic Task Status Checker Styles */
.task-status-container {
  padding: 2rem;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  min-height: calc(100vh - 160px);
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  overflow: hidden;
}

.task-status-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(120, 219, 226, 0.3) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

.task-status-header {
  text-align: center;
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
}

.task-status-header h2 {
  margin: 0;
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 300;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
  background: linear-gradient(45deg, #ffffff, #b794f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.page-navigation {
  margin-bottom: 1.5rem;
  text-align: center;
}

.page-navigation a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
}

.page-navigation a:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.task-status-form {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  max-width: 600px;
  margin: 0 auto;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 1;
}

.task-status-form::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.02) 50%, transparent 70%);
  pointer-events: none;
  border-radius: 20px;
}

.task-status-form:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.2);
}

.form-content {
  position: relative;
  z-index: 1;
}

.task-input-group {
  margin-bottom: 2rem;
}

.task-input-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #ffffff;
  font-weight: 500;
  font-size: 1.1rem;
}

.task-input {
  width: 100%;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: #ffffff;
  font-size: 1rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.task-input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

.task-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.check-button {
  background: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  border: none;
  padding: 1rem 2rem;
  color: white;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 500;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  width: 100%;
  margin-bottom: 1rem;
}

.check-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.6);
  background: linear-gradient(45deg, #764ba2 0%, #667eea 100%);
}

.check-button:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.loading-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: #4ade80;
  font-weight: 500;
  margin: 1rem 0;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(74, 222, 128, 0.3);
  border-radius: 50%;
  border-top-color: #4ade80;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.status-result {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 1.5rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.status-result h3 {
  margin: 0 0 1rem 0;
  color: #ffffff;
  font-size: 1.3rem;
  font-weight: 400;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

.status-json {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 1rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: #e0e0e0;
  overflow-x: auto;
  white-space: pre-wrap;
  line-height: 1.4;
}

.error-message {
  background: rgba(255, 107, 107, 0.1);
  border: 1px solid rgba(255, 107, 107, 0.3);
  border-radius: 12px;
  padding: 1rem;
  margin-top: 1rem;
  color: #ff6b6b;
  backdrop-filter: blur(10px);
  font-weight: 500;
}

.task-examples {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 2rem;
}

.task-examples h4 {
  margin: 0 0 1rem 0;
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 400;
}

.task-examples ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.task-examples li {
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.task-examples li:last-child {
  border-bottom: none;
}

.task-examples code {
  background: rgba(0, 0, 0, 0.3);
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  color: #4ade80;
  font-size: 0.85rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .task-status-container {
    padding: 1rem;
  }

  .task-status-header h2 {
    font-size: 2rem;
  }

  .task-status-form {
    padding: 1.5rem;
    margin: 0 1rem;
  }

  .task-input {
    padding: 0.8rem;
  }

  .check-button {
    padding: 0.8rem 1.5rem;
  }

  .status-json {
    font-size: 0.8rem;
    padding: 0.8rem;
  }

  .task-examples {
    padding: 1rem;
    margin-top: 1.5rem;
  }
}