/* Model Registry - 2025 Futuristic Styling */
.model-registry-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 2rem;
  position: relative;
  overflow-x: hidden;
}

.model-registry-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(120, 255, 198, 0.2) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
}

.model-registry-header {
  text-align: center;
  margin-bottom: 2rem;
}

.model-registry-header h2 {
  font-size: 2.5rem;
  font-weight: 300;
  background: linear-gradient(45deg, #00ffff, #ff00ff, #ffff00);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease-in-out infinite;
  margin: 0;
  text-shadow: 0 0 20px rgba(0, 255, 255, 0.5);
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

/* Navigation */
.page-navigation {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.page-navigation a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  font-weight: 500;
}

.page-navigation a:hover {
  color: #00ffff;
  background: rgba(0, 255, 255, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 255, 255, 0.3);
}

.page-navigation a.active {
  color: #00ffff;
  background: rgba(0, 255, 255, 0.2);
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.5);
}

/* Registry Section */
.registry-section {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  margin-bottom: 2rem;
}

.registry-section h3 {
  color: #00ffff;
  font-size: 1.8rem;
  font-weight: 300;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 10px rgba(0, 255, 255, 0.5);
}

.registry-section h4 {
  color: #ff00ff;
  font-size: 1.4rem;
  font-weight: 300;
  margin: 1.5rem 0 1rem 0;
  text-shadow: 0 0 10px rgba(255, 0, 255, 0.5);
}

/* Form Elements */
.form-row {
  margin-bottom: 1.5rem;
}

.model-type-selector {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.selector-label {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 1rem;
}

.model-select {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  color: #ffffff;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  min-width: 150px;
}

.model-select:focus {
  outline: none;
  border-color: #00ffff;
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.15);
}

.model-select option {
  background: #1a1a2e;
  color: #ffffff;
}

/* Action Buttons */
.action-buttons {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.action-button,
.benchmark-button,
.automation-button {
  background: linear-gradient(45deg, rgba(0, 255, 255, 0.2), rgba(255, 0, 255, 0.2));
  border: 1px solid rgba(0, 255, 255, 0.3);
  border-radius: 15px;
  color: #ffffff;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
}

.action-button:hover,
.benchmark-button:hover,
.automation-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 20px rgba(0, 255, 255, 0.4);
  border-color: rgba(0, 255, 255, 0.6);
  background: linear-gradient(45deg, rgba(0, 255, 255, 0.3), rgba(255, 0, 255, 0.3));
}

.action-button:disabled,
.benchmark-button:disabled,
.automation-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.benchmark-button {
  background: linear-gradient(45deg, rgba(255, 255, 0, 0.2), rgba(255, 165, 0, 0.2));
  border-color: rgba(255, 255, 0, 0.3);
}

.benchmark-button:hover {
  box-shadow: 0 5px 20px rgba(255, 255, 0, 0.4);
  border-color: rgba(255, 255, 0, 0.6);
  background: linear-gradient(45deg, rgba(255, 255, 0, 0.3), rgba(255, 165, 0, 0.3));
}

.automation-button {
  background: linear-gradient(45deg, rgba(0, 255, 0, 0.2), rgba(0, 255, 255, 0.2));
  border-color: rgba(0, 255, 0, 0.3);
}

.automation-button:hover {
  box-shadow: 0 5px 20px rgba(0, 255, 0, 0.4);
  border-color: rgba(0, 255, 0, 0.6);
  background: linear-gradient(45deg, rgba(0, 255, 0, 0.3), rgba(0, 255, 255, 0.3));
}

/* Loading Indicator */
.loading-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  color: #00ffff;
  font-weight: 500;
  padding: 1rem;
  background: rgba(0, 255, 255, 0.1);
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(0, 255, 255, 0.2);
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(0, 255, 255, 0.3);
  border-top: 2px solid #00ffff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Error Message */
.error-message {
  background: rgba(255, 0, 0, 0.1);
  border: 1px solid rgba(255, 0, 0, 0.3);
  border-radius: 10px;
  color: #ff6b6b;
  padding: 1rem;
  margin-bottom: 1rem;
  backdrop-filter: blur(10px);
  text-align: center;
  font-weight: 500;
}

/* Models Table */
.models-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 2rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.models-table th,
.models-table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.models-table th {
  background: rgba(0, 255, 255, 0.1);
  color: #00ffff;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.9rem;
  letter-spacing: 0.5px;
}

.models-table tbody tr {
  transition: all 0.3s ease;
}

.models-table tbody tr:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: scale(1.01);
}

.models-table tbody tr:last-child td {
  border-bottom: none;
}

/* Status Indicators */
.status-active {
  color: #00ff00;
  font-weight: 600;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
}

.status-inactive {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 500;
}

/* Table Buttons */
.table-button {
  background: rgba(0, 255, 255, 0.2);
  border: 1px solid rgba(0, 255, 255, 0.3);
  border-radius: 8px;
  color: #ffffff;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
}

.table-button:hover {
  background: rgba(0, 255, 255, 0.3);
  transform: translateY(-1px);
  box-shadow: 0 3px 10px rgba(0, 255, 255, 0.3);
}

.table-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.metrics-button {
  background: rgba(255, 0, 255, 0.2);
  border-color: rgba(255, 0, 255, 0.3);
}

.metrics-button:hover {
  background: rgba(255, 0, 255, 0.3);
  box-shadow: 0 3px 10px rgba(255, 0, 255, 0.3);
}

/* Results Section */
.results-section {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 15px;
  padding: 2rem;
  margin-bottom: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.results-title {
  color: #00ffff;
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 10px rgba(0, 255, 255, 0.5);
}

/* Comparison Grid */
.comparison-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.comparison-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.comparison-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.comparison-card h4 {
  color: #ff00ff;
  font-size: 1.2rem;
  font-weight: 300;
  margin-bottom: 1rem;
  text-shadow: 0 0 10px rgba(255, 0, 255, 0.5);
}

.metric-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.metric-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.metric-item:last-child {
  border-bottom: none;
}

.metric-value {
  color: #00ffff;
  font-weight: 600;
  font-family: 'Courier New', monospace;
}

.metric-difference {
  font-weight: 600;
  font-family: 'Courier New', monospace;
}

.metric-item.positive .metric-difference {
  color: #00ff00;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
}

.metric-item.negative .metric-difference {
  color: #ff6b6b;
  text-shadow: 0 0 5px rgba(255, 107, 107, 0.5);
}

/* Benchmarks and Automation Content */
.benchmarks-content,
.automation-content {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  padding: 1rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.9);
  white-space: pre-wrap;
  overflow-x: auto;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.automation-results {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.automation-item h4 {
  color: #00ff00;
  font-size: 1.1rem;
  font-weight: 300;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

/* Metrics Section */
.metrics-section {
  margin-top: 2rem;
}

.metrics-section h4 {
  color: #ffff00;
  font-size: 1.2rem;
  font-weight: 300;
  margin-bottom: 1rem;
  text-shadow: 0 0 10px rgba(255, 255, 0, 0.5);
}

.metrics-raw {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  padding: 1rem;
  font-family: 'Courier New', monospace;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.9);
  white-space: pre-wrap;
  overflow-x: auto;
  border: 1px solid rgba(255, 255, 255, 0.1);
  max-height: 400px;
  overflow-y: auto;
}

/* Hint Text */
.hint-text {
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
  text-align: center;
  margin-top: 2rem;
  font-size: 0.9rem;
}

/* Footer */
.footer {
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.model-path {
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.8);
  word-break: break-all;
  max-width: 200px;
}
@media (max-width: 768px) {
  .model-registry-container {
    padding: 1rem;
  }

  .model-registry-header h2 {
    font-size: 2rem;
  }

  .page-navigation {
    gap: 0.5rem;
  }

  .page-navigation a {
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }

  .registry-section {
    padding: 1rem;
  }

  .action-buttons {
    flex-direction: column;
    gap: 0.5rem;
  }

  .action-button,
  .benchmark-button,
  .automation-button {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }

  .comparison-grid {
    grid-template-columns: 1fr;
  }

  .models-table {
    font-size: 0.9rem;
  }

  .models-table th,
  .models-table td {
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  .model-type-selector {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .model-select {
    width: 100%;
    max-width: 200px;
  }

  .results-section {
    padding: 1rem;
  }

  .comparison-card {
    padding: 1rem;
  }
}