/* 2025 Futuristic Predictive Intelligence Styles */
.predictive-container {
  padding: 2rem;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  min-height: calc(100vh - 160px);
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  overflow: hidden;
}

.predictive-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(120, 219, 226, 0.3) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

.predictive-header {
  text-align: center;
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
}

.predictive-header h2 {
  margin: 0;
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 300;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
  background: linear-gradient(45deg, #ffffff, #b794f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.page-navigation {
  margin-bottom: 1.5rem;
  text-align: center;
}

.page-navigation a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
}

.page-navigation a:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.predictive-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  position: relative;
  z-index: 1;
}

.predictive-section {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.predictive-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.02) 50%, transparent 70%);
  pointer-events: none;
}

.predictive-section:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.2);
}

.predictive-section h3 {
  margin: 0 0 1.5rem 0;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 300;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

.predictive-section h4 {
  margin: 0 0 1rem 0;
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 400;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

.predictive-form {
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #ffffff;
  font-weight: 500;
  font-size: 1rem;
}

.form-textarea {
  width: 100%;
  min-height: 120px;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: #ffffff;
  font-size: 1rem;
  font-family: 'Courier New', monospace;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  box-sizing: border-box;
  resize: vertical;
}

.form-textarea:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

.form-textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-family: inherit;
}

.action-button {
  background: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  border: none;
  padding: 1rem 2rem;
  color: white;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 500;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  width: 100%;
}

.action-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.6);
  background: linear-gradient(45deg, #764ba2 0%, #667eea 100%);
}

.action-button:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.audit-button {
  background: linear-gradient(45deg, #f59e0b 0%, #d97706 100%);
  box-shadow: 0 4px 15px rgba(245, 158, 11, 0.4);
}

.audit-button:hover:not(:disabled) {
  background: linear-gradient(45deg, #d97706 0%, #f59e0b 100%);
  box-shadow: 0 8px 25px rgba(245, 158, 11, 0.6);
}

.results-container {
  margin-top: 2rem;
}

.results-json {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: #e0e0e0;
  overflow-x: auto;
  white-space: pre-wrap;
  line-height: 1.4;
  max-height: 400px;
  overflow-y: auto;
}

.results-json::-webkit-scrollbar {
  width: 6px;
}

.results-json::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.results-json::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.results-json::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

.results-placeholder {
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  padding: 2rem;
  font-style: italic;
  font-size: 1rem;
}

.loading-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: #4ade80;
  font-weight: 500;
  padding: 2rem;
}

.spinner {
  width: 24px;
  height: 24px;
  border: 3px solid rgba(74, 222, 128, 0.3);
  border-radius: 50%;
  border-top-color: #4ade80;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error-message {
  background: rgba(255, 107, 107, 0.1);
  border: 1px solid rgba(255, 107, 107, 0.3);
  border-radius: 12px;
  padding: 1rem;
  margin-bottom: 1rem;
  color: #ff6b6b;
  backdrop-filter: blur(10px);
  font-weight: 500;
}

.json-examples {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 1.5rem;
}

.json-examples h5 {
  margin: 0 0 1rem 0;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 400;
}

.json-examples code {
  background: rgba(0, 0, 0, 0.3);
  padding: 0.5rem;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
  color: #4ade80;
  display: block;
  margin-bottom: 0.5rem;
  white-space: pre-wrap;
}

/* Responsive design */
@media (max-width: 768px) {
  .predictive-container {
    padding: 1rem;
  }

  .predictive-header h2 {
    font-size: 2rem;
  }

  .predictive-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .predictive-section {
    padding: 1.5rem;
  }

  .form-textarea {
    min-height: 100px;
    padding: 0.8rem;
  }

  .action-button {
    padding: 0.8rem 1.5rem;
  }

  .results-json {
    font-size: 0.8rem;
    padding: 1rem;
  }
}